<template>
  <div class="container position-relative">
    <b-overlay no-wrap :show="isLoading" rounded="sm" />
    <div v-if="moduleList.length>1" class="mb-3">
      <template v-for="(m,i) in moduleList">
        <button @click="activeModule = m.id" :key="i+'-modules'" class="btn font-weight-bold font-size-md mr-3 py-4 min-w-150px" :class="activeModule === m.id? 'btn-primary': 'btn-link-primary'">{{ m.label }}</button>
      </template>
    </div>
    <!-- <pre>{{ mainData }}</pre> -->
    <div class="row mb-3">
      <div class="col-4 pr-md-0">
        <div class="card card-custom card-stretch">
          <div class="card-body">
            <div class="row">
              <div class="form-group position-relative col-12 mb-5">
                <span class="d-block mb-2 font-weight-bolder" for="">Pilih Divisi / AP</span>
                <v-select :disabled="datasetList.division.disabled" :clearable="false" v-model="mainForm.division_id" :appendToBody="true" class="vs-style custom" :options="datasetList.division.data" :placeholder="'Pilih salah satu Divisi'">
                  <slot name="no-options">{{ $t('error.no_data') }}</slot>
                </v-select>
                <b-overlay no-wrap :show="activeModule && datasetList.division.disabled" :z-index="9" rounded="sm" :opacity=".95">
                  <template #overlay>
                    <div class="d-flex flex-fill align-items-center justify-content-center font-weight-bold min-w-350px">
                      <i class="ri-lock-line ri-2x font-weight-bold text-danger"></i>
                      <span class="flex-fill pl-2">Tidak ada Divisi / AP di {{ moduleList.filter(x => x.id === activeModule)[0].label }} </span>
                    </div>
                  </template>
                </b-overlay>
              </div>
              <div class="form-group col-12 mb-0">
                <span class="d-block mb-2 font-weight-bolder" for="">Tahun</span>
                <date-picker :clearable="false" :input-attr="{ autocomplete:'off' }" v-model="mainForm.periode_year" type="year" :input-class="'form-control custom-dateyears'" :value-type="formatDate('year')" :formatter="momentFormat" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col">
        <div class="card card-custom card-stretch">
          <div class="card-body py-5 pr-5">
            <!-- <b-overlay no-wrap :show="isLoading" rounded="sm" /> -->
            <div class="row no-gutters align-items-center">
              <div class="col p-0">
                <div class="form-group mb-5">
                  <span class="d-block mb-2 font-weight-bolder" for="">Pilih Agenda Input</span>
                  <v-select :disabled="false" :clearable="false" v-model="mainForm.agenda_id" :appendToBody="true" class="vs-style custom" :options="datasetList.agenda.data" :placeholder="'Pilih salah satu Agenda'">
                    <slot name="no-options">{{ $t('error.no_data') }}</slot>
                  </v-select>
                </div>
                <div class="form-group position-relative mb-0">
                  <span class="d-block mb-1 font-weight-bolder" for="">Pilih Skenario Input</span>
                  <div class="checkbox-inline py-2">
                    <label v-for="(f,i) in datasetList.scenario.data" :key="i+'-form-a'" class="checkbox checkbox-lg checkbox-outline mb-1">
                      <input :disabled="globalFilter.agenda_id === 0" v-model="mainForm.scenario_input" :value="f" type="radio">
                      <span></span>{{ f.label }}</label>
                  </div>
                  <b-overlay no-wrap :show="globalFilter.agenda_id === 0" :z-index="9" rounded="sm" :opacity=".95">
                    <template #overlay>
                      <div class="d-flex flex-fill align-items-center justify-content-center font-weight-bold min-w-350px">
                        <i class="ri-lock-line ri-2x font-weight-bold text-danger"></i>
                        <span class="flex-fill pl-2">Tidak ada Skenario Input di RKAP</span>
                      </div>
                    </template>
                  </b-overlay>
                </div>
              </div>
              <div class="download-div ml-5 p-5 position-relative">
                <span class="d-block mb-2 font-weight-bolder text-primary" for="">Download File Template</span>
                <p class="font-size-sm">File ini memiliki kolom heading sesuai dengan data yang diperlukan untuk mengimport data dengan benar</p>
                <button @click="downloadImport()" class="btn btn-outline-primary btn-sm font-weight-bold">
                  <i class="ri-download-2-line"></i> Download Template </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <template v-if="activeModule && moduleList.filter(x=> x.id === activeModule)[0].queue">
      <queue-import ref="queue-import" @done="getMainData(globalFilter)" :key="queueKey" :filters="globalFilter" :API="moduleList.filter(x=> x.id === activeModule)[0].queue" />
    </template>
    <div :class="fullscreen? 'fullscreen-component': 'mt-3'" class="position-relative">
      <!-- <b-overlay no-wrap :show="isLoading" rounded="sm" /> -->
      <div class="card card-custom">
        <div class="card-header border-0 p-5" style="min-height:50px">
          <div class="d-flex flex-fill flex-wrap align-items-center justify-content-between">
            <div class="flex-fill">
              <h3 class="card-title font-weight-bolder text-dark ">{{ config.title }} <template v-if="moduleList.length>1 && activeModule"> - {{ moduleList.filter(x => x.id === activeModule)[0].label }}</template></h3>
            </div>
            <div class="d-flex flex-wrap align-items-center">
              <a @click="fullscreen = !fullscreen" class="btn btn-link text-primary mr-1">
                <i :class="fullscreen? 'ri-fullscreen-exit-line' : 'ri-fullscreen-line'" class="text-primary"></i>
                <span>Fullscreen</span>
              </a>
              <button @click="$refs.inputFile.click()" class="btn btn-primary font-size-sm font-weight-bolder"><i class="ri-upload-2-line"></i> Upload File </button>
              <input ref="inputFile" type="file" hidden @change="uploadImport($event)" accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel">
            </div>
          </div>
        </div>
        <div class="card-body px-5 pt-0 pb-0">
          <b-tabs :content-class="mainData.length>1? 'mt-3' : 'mt-0'" :nav-wrapper-class="mainData.length>1? '' : 'd-none'" active-nav-item-class="active-nav-title font-weight-bolder text-primary">
            <b-tab v-for="(d,i) in mainData" :key="i+'-table-data'" :title="d.label" active>
              <div v-if="d.data.length" class="table-responsive sticky-table">
                <table class="table table-head-custom table-bordered table-vertical-cente table-input">
                  <thead>
                    <tr class="bg-light-info">
                      <th class="nowrap-table text-center" style="color: #000 !important;vertical-align:middle !important;">No.</th>
                      <template v-for="(th, j) in d.config.fields">
                        <th :class="[th.class, 'min-w-100px']" :key="j+'-th-data'" style="color: #000 !important;vertical-align:middle !important;">
                          <div style="white-space: pre-wrap;">{{ th.label }}</div>
                        </th>
                      </template>
                      <th v-if="d.config.total" :colspan="d.config.monthly.isPercent? 2: 1" class="nowrap-table min-w-150px text-center" style="color: #000 !important;vertical-align:middle !important;">
                        <span>Total</span>
                      </th>
                      <th :colspan="d.config.monthly.isPercent? 2: 1" v-for="(thMonth, j) in d.data[0][d.config.monthly.data]" :key="j+'-th-monthly'" :class="globalFilter.agenda_id === -Math.abs(j+1)? 'bg-primary-primary': ''" class="nowrap-table min-w-150px text-center" style="color: #000 !important;vertical-align:middle !important;">
                        <span>{{ thMonth[d.config.monthly.header] | parse(d.config.monthly.parse) }}</span>
                        <span v-if="globalFilter.agenda_id!==0" class="font-size-xs d-block text-info"> {{ Math.abs(globalFilter.agenda_id) >= j+1 ? 'Actual': 'Forecast' }} </span>
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <template v-for="(data, k) in d.data">
                      <tr :key="'-mock'+k+'-data'">
                        <td class="nowrap-table text-center">{{ k+1 }}</td>
                        <template v-for="(th, l) in d.config.fields">
                          <td v-if="!th.wrap" :class="th.class" :key="l+'-th-td-data'">
                            <div v-if="!th.isMoney">{{ data[th.data] | parse(th.pipe) }}</div>
                            <div v-else class="d-flex align-items-center justify-content-between">
                              <span class="pr-3">Rp.</span>
                              <span>{{ data[th.data] | parse(th.pipe) }}</span>
                            </div>
                          </td>
                          <template v-else>
                            <td v-if="k===0 || data[th.data] !== d.data[k-1][th.data] || data[d.config.pk] !== d.data[k-1][d.config.pk]" :rowspan="d.data.filter(x => x[d.config.pk] === data[d.config.pk]).length" :class="th.class" :key="l+'-th-td-data'">
                              <div v-if="!th.isMoney">{{ data[th.data] | parse(th.pipe) }}</div>
                              <div v-else class="d-flex align-items-center justify-content-between">
                                <span class="pr-3">Rp.</span>
                                <span>{{ data[th.data] | parse(th.pipe) }}</span>
                              </div>
                            </td>
                          </template>
                        </template>
                        <template v-if="d.config.total">
                          <template v-if="!d.config.total.wrap">
                            <td class="nowrap-table min-w-150px text-center">
                              <div v-if="!d.config.monthly.isMoney">{{ data['total'] | parse(d.config.monthly.pipe) }}</div>
                              <div v-else class="d-flex align-items-center justify-content-between">
                                <span class="pr-3">Rp.</span>
                                <span>{{ data['total'] | parse(d.config.monthly.pipe) }}</span>
                              </div>
                            </td>
                            <td v-if="d.config.monthly.isPercent" class="nowrap-table text-center">
                              <div>{{ data['total_prosentase'] }} %</div>
                            </td>
                          </template>
                          <template v-else>
                            <td v-if="k===0 || data['total'] !== d.data[k-1]['total'] || data[d.config.pk] !== d.data[k-1][d.config.pk]" :rowspan="d.data.filter(x => x[d.config.pk] === data[d.config.pk]).length" class="nowrap-table min-w-150px text-center">
                              <div v-if="!d.config.monthly.isMoney">{{ data['total'] | parse(d.config.monthly.pipe) }}</div>
                              <div v-else class="d-flex align-items-center justify-content-between">
                                <span class="pr-3">Rp.</span>
                                <span>{{ data['total'] | parse(d.config.monthly.pipe) }}</span>
                              </div>
                            </td>
                            <td v-if="d.config.monthly.isPercent && k===0 || data['total'] !== d.data[k-1]['total'] || data[d.config.pk] !== d.data[k-1][d.config.pk]" :rowspan="d.data.filter(x => x[d.config.pk] === data[d.config.pk]).length" class="nowrap-table text-center">
                              <div>{{ data['total_prosentase'] }} %</div>
                            </td>
                          </template>
                        </template>
                        <template v-for="(thMonth, j) in d.data[0][d.config.monthly.data]">
                          <td :key="j+'-th-td-monthly-value'" :class="globalFilter.agenda_id === -Math.abs(j+1)? 'bg-primary-primary': ''" class="nowrap-table min-w-150px text-center">
                            <div v-if="!d.config.monthly.isMoney">{{ data[d.config.monthly.data][j]['value'] | parse(d.config.monthly.pipe) }}</div>
                            <div v-else class="d-flex align-items-center justify-content-between">
                              <span class="pr-3">Rp.</span>
                              <span>{{ data[d.config.monthly.data][j]['value'] | parse(d.config.monthly.pipe) }}</span>
                            </div>
                          </td>
                          <td v-if="d.config.monthly.isPercent" :key="j+'-th-td-monthly-percent'" :class="globalFilter.agenda_id === -Math.abs(j+1)? 'bg-primary-primary': ''" class="nowrap-table text-center">
                            <div>{{ data[d.config.monthly.data][j]['prosentase'] }} %</div>
                          </td>
                        </template>
                      </tr>
                    </template>
                  </tbody>
                   <tfoot v-if="d.config.subtotal && d.data_summary">
                    <tr class="bg-light-info">
                      <th class=""></th>
                      <template v-for="(th, j) in d.config.fields">
                        <th :key="j+'-subtotal-footer'" :class="[th.class, 'min-w-100px text-right']">
                          <span class="" v-if="th.class.search('sl')>=0">Subtotal</span>
                        </th>
                      </template>
                      <th v-if="d.config.total" :colspan="d.config.monthly.isPercent? 2: 1" class="nowrap-table min-w-150px text-center">
                        <div v-if="!d.config.monthly.isMoney">{{ d.data_summary['total'] | parse(d.config.monthly.pipe) }}</div>
                        <div v-else class="d-flex align-items-center justify-content-between">
                          <span class="pr-3">Rp.</span>
                          <span>{{ d.data_summary['total'] | parse(d.config.monthly.pipe) }}</span>
                        </div>
                      </th>
                      <th :colspan="d.config.monthly.isPercent? 2: 1" v-for="(thMonth, j) in d.data[0][d.config.monthly.data]" :key="j+'-tfoot-monthly'" :class="globalFilter.agenda_id === -Math.abs(j+1)? 'bg-primary-primary': ''" class="nowrap-table min-w-150px text-center" style="color: #000 !important;vertical-align:middle !important;">
                        <div v-if="!d.config.monthly.isMoney">{{ d.data_summary[$_sys.numberMonth(mainForm.periode_year, j+1)] | parse(d.config.monthly.pipe) }}</div>
                        <div v-else class="d-flex align-items-center justify-content-between">
                          <span class="pr-3">Rp.</span>
                          <span>{{ d.data_summary[$_sys.numberMonth(mainForm.periode_year, j+1)] | parse(d.config.monthly.pipe) }}</span>
                        </div>
                      </th>
                    </tr>
                  </tfoot>
                </table>
              </div>
            </b-tab>
          </b-tabs>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
moment.locale('id')
export default {
  name: 'input-balance-sheet',
  data () {
    return {
      isLoading: false,
      fullscreen: false,
      config: {
        title: 'Input Balance Sheet (BS)',
        permission: {
          read: 'template-allow-all'
        }
      },
      momentFormat: {
        stringify: (date) => { return date ? moment(date).format('YYYY') : '' },
        parse: (value) => { return value ? moment(value, 'YYYY').toDate() : null }
      },
      searchFilter: '',
      globalFilter: {},
      mainData: [],
      mainForm: {},
      moduleList: [
        {
          id: 'KP',
          label: 'Kantor Pusat',
          export: 'export-excel/template-bs-kp',
          import: 'custom/data-input-bs-kp/import-template',
          queue: 'import-data-input-bs-kp',
          permission: 'import-data-input-bs-kp',
          dataset: [
            {
              // eslint-disable-next-line object-property-newline
              id: 'bskp', endpoint: 'custom/data-input-bs-kp-components', label: '', type: 'component', pk: 'component_name',
              fields: [
                { data: 'component_name', label: 'Komponen', isMoney: false, class: 'sl', action: true },
                { data: 'rel_component_id', label: 'Kode', isMoney: false, class: 'nowrap-table text-center' }
              ],
              // eslint-disable-next-line object-property-newline
              monthly: { data: 'data_series_months', header: 'periode_month', parse: 'longMonth', pipe: 'number', isMoney: false, isPercent: false }, total: true, subtotal: true
            }
          ]
        },
        {
          id: 'AP',
          label: 'Anak Perusahaan',
          export: 'export-excel/template-bs-ap',
          import: 'custom/data-input-bs-ap/import-template',
          queue: 'import-data-input-bs-ap',
          permission: 'import-data-input-bs-ap',
          dataset: [
            {
              // eslint-disable-next-line object-property-newline
              id: 'bsap', endpoint: 'custom/data-input-bs-ap-components', label: '', type: 'component', pk: 'component_name',
              fields: [
                { data: 'component_name', label: 'Komponen', isMoney: false, class: 'sl', action: true },
                { data: 'rel_component_id', label: 'Kode', isMoney: false, class: 'nowrap-table text-center' }
              ],
              // eslint-disable-next-line object-property-newline
              monthly: { data: 'data_series_months', header: 'periode_month', parse: 'longMonth', pipe: 'number', isMoney: false, isPercent: false }, total: true, subtotal: true
            }
          ]
        },
        {
          id: 'BUJT',
          label: 'BUJT',
          export: 'export-excel/template-bs-bujt',
          import: 'custom/data-input-bs-bujt/import-template',
          queue: 'import-data-input-bs-bujt',
          permission: 'import-data-input-bs-bujt',
          dataset: [
            {
              // eslint-disable-next-line object-property-newline
              id: 'bsbujt', endpoint: 'custom/data-input-bs-bujt-components', label: '', type: 'component', pk: 'component_name',
              fields: [
                { data: 'component_name', label: 'Komponen', isMoney: false, class: 'sl', action: true },
                { data: 'rel_component_id', label: 'Kode', isMoney: false, class: 'nowrap-table text-center' }
              ],
              // eslint-disable-next-line object-property-newline
              monthly: { data: 'data_series_months', header: 'periode_month', parse: 'longMonth', pipe: 'number', isMoney: false, isPercent: false }, total: true, subtotal: true
            }
          ]
        },
        {
          id: 'JK',
          label: 'JAKON',
          export: 'export-excel/template-bs-jakon',
          import: 'custom/data-input-bs-jakon/import-template',
          queue: 'import-data-input-bs-jakon',
          permission: 'import-data-input-bs-jakon',
          dataset: [
            {
              // eslint-disable-next-line object-property-newline
              id: 'bsjk', endpoint: 'custom/data-input-bs-jakon-components', label: 'Komponen', type: 'component', pk: 'component_name',
              fields: [
                { data: 'component_name', label: 'Komponen', isMoney: false, class: 'sl', action: true },
                { data: 'rel_component_id', label: 'Kode', isMoney: false, class: 'nowrap-table text-center' }
              ],
              // eslint-disable-next-line object-property-newline
              monthly: { data: 'data_series_months', header: 'periode_month', parse: 'longMonth', pipe: 'number', isMoney: true, isPercent: false }, total: true, subtotal: true
            },
            {
              // eslint-disable-next-line object-property-newline
              id: 'bsjk', endpoint: 'custom/data-input-bs-jakon-prosentase', label: '% Konversi Piutang Tabrut', type: 'component', pk: 'rel_prosentase_component_id',
              fields: [
                { data: 'rel_prosentase_component_id', label: 'Komponen', isMoney: false, class: 'sl', action: true }
              ],
              // eslint-disable-next-line object-property-newline
              monthly: { data: 'data_series_months', header: 'periode_month', parse: 'longMonth', pipe: 'suffix-%', isMoney: false, isPercent: false }, total: true, subtotal: true
            }
          ]
        }
      ],
      moduleDataset: {
        KP: { division: { disabled: true, api: 'divisions' } },
        AP: { division: { disabled: false, api: 'custom/division-ap' } },
        BUJT: { division: { disabled: false, api: 'custom/division-bujt' } },
        JK: { division: { disabled: false, api: 'custom/division-jakon' } }
      },
      activeModule: null,
      datasetList: {
        division: { disabled: true, api: null, param: { sort: 'ASC', sort_by: 'division_id' }, option: { label: 'division_name', code: 'id' }, data: [], target: 'division_id' },
        agenda: { disabled: false, api: null, param: null, option: null, data: [], target: null },
        scenario: { disabled: false, api: null, param: null, option: null, data: [{ code: 'pesimist', label: 'Skenario Pesimis' }, { code: 'moderate', label: 'Skenario Moderat' }, { code: 'optimist', label: 'Skenario Optimis' }], target: null }
      },
      isUploading: false,
      uploadMax: 1,
      uploadLimitExceed: false,
      uploadError: null,
      uploadPercentage: 0,
      tableConfig: [],
      queueKey: 0
    }
  },
  watch: {
    mainForm: {
      deep: true,
      immediate: false,
      handler (e) {
        if (e) {
          let newFilter = Object.assign({}, this.globalFilter, {
            agenda_id: e.agenda_id.code,
            division_id: e.division_id.code,
            periode_year: moment(moment(e.periode_year, 'YYYY').toDate()).format('YYYY-MM-DD'),
            scenario_input: e.scenario_input.code
          })
          if (this.datasetList.division.disabled) delete newFilter.division_id
          this.globalFilter = Object.assign({}, newFilter)
        }
      }
    },
    activeModule: {
      deep: false,
      immediate: true,
      handler (e) {
        if (this.moduleDataset[e]) {
          this.$set(this.datasetList, 'division', { ...this.datasetList.division, ...this.moduleDataset[e].division })
          this.getDataset()
          this.queueKey++
        }
      }
    }
  },
  async mounted () {
    this.$_sys.togleClass('kt_body', 'aside-minimize', true)
    this.$store.commit('set', ['sideNavSecondary', false])
    await this.getDataset('KP')
  },
  methods: {
    staticDataset () {
      let result = []
      for (let i = 0; i < 13; i++) {
        const e = -Math.abs(i)
        result.push({ label: i === 0 ? 'RKAP' : 'Stress Test ' + moment().set('date', 1).set('month', i - 1).format('MMMM'), code: i === 0 ? i : e })
      }
      this.$set(this.datasetList.agenda, 'data', result)
      this.$set(this.mainForm, 'agenda_id', result[0])
      this.$set(this.mainForm, 'scenario_input', this.datasetList.scenario.data[1])
    },
    async getDataset (setActive = null) {
      this.mainForm = Object.assign({}, {
        division_id: { label: null, code: null },
        periode_year: moment().set('date', 1).set('month', 0).format('YYYY'),
        agenda_id: { label: null, code: null },
        scenario_input: { label: null, code: null }
      })
      for (let i = 0; i < Object.keys(this.datasetList).length; i++) {
        const e = Object.keys(this.datasetList)[i]
        if (this.datasetList[e].api) {
          this.$_api.dataset(this.datasetList[e].api, this.datasetList[e].param).then(res => {
            let result = res.data.map(x => { return { label: x[this.datasetList[e].option.label], code: x[this.datasetList[e].option.code], data: x } })
            this.$set(this.datasetList[e], 'data', result)
            if (this.datasetList[e].target && result.length) this.$set(this.mainForm, this.datasetList[e].target, result[0])
          }, err => {
            this.$_alert.error(err)
          })
        } else this.$set(this.mainForm, this.datasetList[e].target, { label: null, code: null })
      }
      await this.staticDataset()
      if (setActive) this.activeModule = setActive
    },
    downloadImport () {
      this.isLoading = true
      const selected = this.moduleList.filter(x => x.id === this.activeModule)[0]
      let xlsTitle = this.mainForm.division_id.data.division_code
      xlsTitle += '_' + selected.id
      xlsTitle += this.globalFilter.agenda_id === 0 ? '_RKAP' : '_ST-' + moment().set('date', 1).set('month', (Math.abs(this.globalFilter.agenda_id) - 1)).format('MMM') + '_' + this.mainForm.scenario_input.code
      xlsTitle += '_' + moment(this.globalFilter.periode_year).format('YYYY')
      this.$_api.downloadFile(selected.export, this.globalFilter, xlsTitle + '.xlsx').then(res => {
        this.isLoading = false
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    },
    uploadImport (e) {
      this.uploadPercentage = 0
      this.uploadError = null
      const api = this.moduleList.filter(x => x.id === this.activeModule)[0].import
      if ((e.target.files[0].size / 1000000).toFixed(2) < this.uploadMax) {
        this.isLoading = true
        this.uploadLimitExceed = false
        let file = e.target.files[0]
        let formData = new FormData()
        let form = JSON.parse(JSON.stringify(this.globalFilter))
        form.scenario_input = form.agenda_id === 0 ? 'rkap' : form.scenario_input
        for (let i = 0; i < Object.keys(form).length; i++) {
          const e = Object.keys(form)[i]
          formData.append(e, form[e])
        }
        formData.append('file', file)
        this.$_api.uploadFile(api, formData, event => {
          this.uploadPercentage = Math.round((100 * event.loaded) / event.total)
        }).then(res => {
          this.isLoading = false
          e.target.value = null
          this.$refs['queue-import'].refreshData()
        }).catch((err) => {
          this.isLoading = false
          this.uploadError = err.data ? err.data.message : err.message
          e.target.value = null
        })
      } else this.uploadLimitExceed = true
    },
    formatDate (type) {
      let _format
      switch (type) {
        case 'date':
        case 'date-range':
          _format = 'YYYY-MM-DD'
          break
        case 'datetime':
        case 'datetime-range':
          _format = 'YYYY-MM-DD HH:mm:ss'
          break
        case 'year':
        case 'year-range':
          _format = 'YYYY'
          break
        case 'month':
        case 'month-range':
          _format = 'YYYY-MM-DD'
          break
        case 'time':
        case 'time-range':
          _format = 'HH:mm:ss'
          break
        case 'week':
        case 'week-range':
          _format = 'DD'
          break
      }
      return _format
    },
    async getMainData (e) {
      this.mainData = []
      this.isLoading = true
      let form = Object.assign({}, e)
      form.scenario_input = form.agenda_id === 0 ? 'rkap' : form.scenario_input
      const activeModule = this.moduleList.filter(x => x.id === this.activeModule)[0]
      if (activeModule.dataset.length > 1) {
        for (let i = 0; i < activeModule.dataset.length; i++) {
          const el = activeModule.dataset[i]
          await this.$_api.dataset(el.endpoint, form).then(res => {
            res.label = el.label
            res.config = el
            this.mainData.push(res)
          }, err => {
            this.$_alert.error(err)
          })
        } this.isLoading = false
      } else {
        this.$_api.dataset(activeModule.dataset[0].endpoint, form).then(res => {
          res.label = activeModule.dataset[0].label
          res.config = activeModule.dataset[0]
          this.mainData.push(res)
          this.isLoading = false
        }, err => {
          this.isLoading = false
          this.$_alert.error(err)
        })
      }
    }
  }
}
</script>

<style>
  .nav-tabs .nav-link.active.active-nav-title {
    background-color: #fff !important;
  }
</style>
<style scoped>
  .timeline.timeline-6:before {
    left: 1.5px !important;
  }
  .form-control.custom-dateyears {
    height: calc(1.5em + 1.19rem) !important;
  }
  .fullscreen-component {
    position: absolute !important;
    padding: 0 !important;
    top: 0;
    left: 0;
    width: 100vw !important;
    height: 100vh !important;
    z-index: 99999 !important;
    overflow-y: scroll;
  }
  .fullscreen-component .card {
    min-height: 100%;
  }
  .sticky-table {
    max-height: 90vh;
  }
  .sticky-table {
    position: sticky;
    top: -1px;
  }
  .sticky-table table thead tr {
    position: sticky;
    top: -1px;
    z-index: 3;
  }
  .table-input .sr {
    position: sticky;
    right: 1px; /* border */
  }
  .table-input .sl {
    position: sticky;
    left: -1px; /* border */
  }
  .table-input .sl div {
    width: 350px !important;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .table-input th.sl,
  .table-input th.sr {
    background: var(--info-light);
  }
  .table-input td.sl,
  .table-input td.sr {
    background: #fff;
  }
  .table-input td {
    padding: 3px 10px;
  }
  .download-div {
    min-width: 270px;
    max-width: 270px;
    border: 2px solid #ebedf3;
    border-radius: 8px;
  }
  .popover-xl {
    z-index: 999999 !important;
    max-width: 700px !important;
    width: 700px !important;
  }
</style>



